* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Times New Roman', Times, serif;
}

/* head */
.header {
  text-align: center;
  /* border: black 5px ridge; */
  /* background-color: black; */
  color: black;
}

/* first nav botton */
.navBox{
  display: flex;
  justify-content: space-evenly;
  background-color: black;
}
.btn {
  border: black 5px ridge;
  background-color: #fbd1a2;
  padding: 5px;
  margin: 2px;
  color:black;
}
.btn:hover  {
  background-color: #ee6c4d;
  color: black;
}
.addNav {
  display: flex;
}

/* Adding List */
.addNav {
  display: flex;
  flex-wrap: nowrap;
}
.addListIcon  {
  width: 30px;
  margin: 10px 10px 3px;
}
.addForm  {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.addNote  {
  width: 100%;
}
.addQty {
  width:10%;
}
.addDate  {
  background-color: white;
}


/* search add and search */
.navBox2 {
  border-top: blue 2px dashed;
  border-bottom: blue 2px dashed;
  background-color: darkturquoise ;;
  padding: 5px;
  display: flex;
  justify-content: space-around;
}
.searchIcon {
  width:15px;
}

/* table */
.table {
  text-align: center;
  padding: 5px;
  margin: 0 auto;
}
.tHead  {
  background-color: darkturquoise;;
  color: black;
}
.tHeadtxt {
  padding: 5px;
  border: 2px solid black;
  color: black;
}
.tBody  {
  background-color:lightgray ;
}
.tData  {
  padding: 5px;
}
.tDataBtn {
  display: flex;
  justify-content: space-between;
  padding: 3px;
}
tr:nth-child(even) {background-color: lightgoldenrodyellow;}

/* left side content */
.leftSide{
  margin: 5px;
}

/* buy list */
.buyBox1 {
  border: 5px groove darkviolet;
  background-color: darkturquoise;

}
.buyBox {
  border: 3px solid black;
  background-color: #74bbfb;
  color: black;
  margin: 5px;
}
.buyName  {
  /* border-bottom: 3px solid black; */
  display: flex;
  justify-content: flex-end;
  padding: 3px;
}
.closeIcon1 {
  margin-left: 15px;
  width: 25px;
}
.buyContent {
  padding: 3px;
  color: black
}
.buyList  {
  list-style-position: inside;
  list-style-type: square;
  padding: 5px;
  display: flex;
}
.checkIcon2 {
  margin-left: 15px;
  width: 20px;
}

/* favorite list */
.favBox1  {
  border: 5px groove green;
  background-color: darkturquoise;
}
.favBox {
  background-color: #74bbfb;
  border: 3px solid black;
  margin: 5px;
  color: black;
}
.favName  {
  display: flex;
  justify-content: flex-end;
  padding: 3px;
}
.closeIcon2 {
  margin-left: 15px;
  width: 25px;
}
.favContent {
  padding: 3px;
  color: black
}
.favList  {
  display: flex;
  list-style-position: inside;
  list-style-type: square;
  padding: 5px;
}
.checkIcon3 {
  margin-left: 15px;
  width: 18px;
}

/* reminder box */
.reminderBox1 {
  border: red ridge 5px;
  background-color: darkturquoise;
}

.reminderBox {
  background-color: #74bbfb;
  border: 3px solid black;
  margin: 5px;
  color: black;
}
.reminderName  {
  /* border-bottom: 3px solid black; */
  display: flex;
  justify-content: flex-end;
  padding: 3px;
}
.closeIcon3 {
  margin-left: 15px;
  width: 25px;
}
.reminderContent {
  padding: 3px;
  color: black
}

/* addReminderFrom */
.addReminderForm  {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  margin: 5px
}
.addReminder2 {
  display: flex;
  flex-wrap: nowrap;
  margin-left: 5px;
  align-content: flex-end;
}
.remindMe   {
  background-color: red;
  padding: 2px;
  margin-top: 25%;
  margin-left: 2px;
  border-radius: 10px;
}
.dateTimePickerReminder {
  background-color: white;
  margin-bottom: 2px;
  width: fit-content;
}
.textArea {
  font-family: monospace;
  padding: 5px;
}

/* Reminder List */
.noteContainer  {
  text-align: center;
  margin: 2px;
}
.reminderMsg  {
  font-family: monospace;
  font-size: 20px;
}
hr  {
  border: black dashed 1px;
}
.reminderIcons  {
  margin: 2px;
}

/* trash box */
.trashBox1  {
  background-color: darkturquoise;
  border: 5px groove plum;
  
}
.trashBox {
  background-color: #74bbfb;
  border: 3px solid black;
  margin: 5px;
  color: black;
}
.trashName  {
  display: flex;
  justify-content: flex-end;
  padding: 3px;
}
.closeIcon4 {
  margin-left: 15px;
  width: 25px;
}
.trashContent {
  padding: 3px;
  color: black;
}
.trashList  {
  display: flex;
  list-style-type: none;
  padding: 5px;
}
.checkIcon4 {
  margin-left: 15px;
  width: 15px;
}




@media only screen and (min-width: 556px) {
    
    .table  {
      margin: 0 auto;
    }
    .leftSide {
      width: 100%;
      display: flex;
      flex-wrap: wrap;;
    }
    
    .buyBox, .buyBox1, .favBox, .favBox1, 
    .reminderBox, .trashBox,.trashBox1,  .reminderBox1 {
      width: 50%;
      position: relative;
    }
  }
  
  
  
  @media only screen and (min-width: 991px) {
    
    .table  {
      float: left;
      width: 50%;
    }

    /* left side content */
    .leftSide {
      margin: 3px;
      float: right;
      width: 48%;
    }
    .buyBox, .buyBox1, .favBox, .favBox1, 
    .reminderBox, .trashBox,.trashBox1,  .reminderBox1 {
      width: 100%;
    }
}
